body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
    font-size: 1.4rem;
    background-color: #444;
    color: #fff;
    text-align: center;

    background: #232526;
    background: linear-gradient(to right, #161616, #000000, #161616);
}

h1, h2 {
    font-weight: 300;
}
.App {
    display: flex;
    flex-direction: column;
}

.Cards {
    display: flex;
    flex-wrap: wrap;
}

.Card {
    flex: 1 1;
    margin: 20px;
    border: 3px solid #000;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;

    display: flex;
    flex-direction: column;
    min-width: 300px;
}

.Card .Content {
    flex: 1 1;
    padding: 15px 0px;
    color: #000;
}

.Card .Footer {
    background-color: #000;
    color: #fff;
    padding: 5px 0px;
}
