body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
    font-size: 1.4rem;
    background-color: #444;
    color: #fff;
    text-align: center;

    background: #232526;
    background: -webkit-linear-gradient(to right, #161616, #000000, #161616);
    background: linear-gradient(to right, #161616, #000000, #161616);
}

h1, h2 {
    font-weight: 300;
}